import React, { Component } from "react";
import DataBox from "../../common/DataBox";
import MRbox from '../../common/MRbox';
import KSHbox from '../../common/KSHbox';
import { emptyChartConfig, multiBarConfig } from '../../../config/chartconfig';
import { getFactoryGPU, getFactoryTestData } from '../../../services/Service';

let boxHeight = 250;

export class Factory extends Component {
  constructor(props) {
    super(props)

    this.nameMap = {
      "1":"实际能耗",
      "2":"理论能耗",
      "4":"实际单耗",
      "5":"理论单耗",
      "9":"实际产量",
      "10":"理论产量"
    };

    let ecc = Object.assign({}, emptyChartConfig, {chart:{height:boxHeight,backgroundColor:"transparent"}} )
  
    this.state = {
       httpLoading: false,
       energyTitle: "实际能耗-理论能耗-对比图-kwh",
       cpuTitle: "实际单耗-理论单耗-对比图-kwh",
       loading: true,
       factory_report:[],
       total_report:[],
       energyChart: ecc,
       cpuChart: ecc
    }
  }

  componentDidMount = () => {
    let query = {
      start: "2018-11-15",
      end: "2018-11-16"
    };
    if (false) {
      this.setState({ httpLoading: true });
      getFactoryGPU(query).then(result => {
        console.log(result);
        this.setState((state, props) => {
          return {
            loading: false,
            factory_report: result.factory_report,
            total_report: result.factory_total,
            energyChart: this.getMultiBarConfig(
              result["factory_report"],
              [1, 2],
              result["real_product_count"],
              result["theoretical_product_count"]
            ),
            cpuChart: this.getMultiBarConfig(result["factory_report"], [4, 5])
          };
        });
        this.setState({ httpLoading: false });
      });
    } else {
      this.setState({ httpLoading: true });
      let result = getFactoryTestData();
      setTimeout(() => {
        this.setState((state, props) => {
          return {
            loading: false,
            factory_report: result.factory_report,
            total_report: result.factory_total,
            energyChart: this.getMultiBarConfig(
              result["factory_report"],
              [1, 2],
              result["real_product_count"],
              result["theoretical_product_count"]
            ),
            cpuChart: this.getMultiBarConfig(result["factory_report"], [4, 5])
          };
        });
        this.setState({ httpLoading: false });
      }, 500);
    }
  }

  genSeries = (data,key,rp,tp) => {
    let rpd = {}, tpd = {};
		let re = {"name":this.nameMap[key[0]], 
				  "data":data.map( value => value[key[0]])};
		let te = {"name":this.nameMap[key[1]], 
				  "data":data.map(value => value[key[1]])};
		if(rp && tp){
			rpd = {
				"name":this.nameMap[9],
				"data":data.map(v => v[0]).fill(rp),
				"type":"line",
				"yAxis":1
			};
			tpd = {
				"name":this.nameMap[10],
				"data":data.map(v => v[0]).fill(tp),
				"type":"line",
				"yAxis":1
			};
			return [re,te,rpd, tpd];
		}else{
			return [re,te];
		}
  }

  getMultiBarConfig = (item, key, rp, tp, empty) => {
    let newchart = Object.assign({}, multiBarConfig);
    newchart.chart.height = boxHeight;
		newchart.series = this.genSeries(item,key, rp, tp);
		newchart.xAxis.categories = item.map( v => v[0]);
		return newchart;
  }

  render() {
    return (
      <div className="row">
        <div className="panel">
          <div className="panel-body">
            <div className="col-md-2">
              <DataBox chart={this.state.energyChart} loading={this.state.loading} />
              <MRbox chart={this.state.cpuChart} loading={this.state.loading} />
              <div className="row">
                <div className="col-md-6">
                  <KSHbox />
                </div>
                <div className="col-md-6">
                  <KSHbox />
                </div>
              </div>
            </div>
            <div className="col-md-3">
              <DataBox chart={this.state.cpuChart} loading={this.state.loading} />
              <MRbox chart={this.state.energyChart} loading={this.state.loading} />
            </div>
            <div className="col-md-5">
              <DataBox chart={this.state.energyChart} loading={this.state.loading} />
              <DataBox chart={this.state.cpuChart} loading={this.state.loading} />
            </div>
            <div className="col-md-2">
              <DataBox chart={this.state.energyChart} loading={this.state.loading} />
              <DataBox chart={this.state.cpuChart} loading={this.state.loading} />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Factory;
